import { http } from '@/api/user'
export default {
    name:'ArticleComments',
    props:{
        article:{
            type:Object,
            required:true
            }
    },
    data(){
        return{
            // 评论列表
            comments:[]
        }
    },
    async mounted(){
        console.log(this.$store.state.user);
        const { data } = await http ({
            type:'GET',
            url:`/api/articles/${this.article.slug}/comments`
        })
        this.comments = data.comments
    }
}
